.hero {
  position: relative;
  height: 690px;
  margin-bottom: 65px;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;

    display: block;
    border: 200px solid transparent;
    border-top: 65px solid #fff;
    border-bottom: transparent; }

  &__slider {
    max-width: 1440px;
    margin: 0 auto; } }

.slider,
.slider__slides,
.slider__slide,
.slide {
  height: 100%; }

.slider {
  position: relative;

  &__dots {
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%); }

  .slick-dots {
    position: static;

    & > li {
      margin: 0 10px;
      transform: rotate3d(0, 0, 1, 45deg);
      background-color: #b2b2b2;

      button {
        opacity: 0; } }

    & > li.slick-active {
      background-color: #000; } } }

.slide {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  background-position: top center;
  background-repeat: no-repeat;

  &__sale {
    width: 280px;
    border: 2px solid #ffffff;
    outline: 1px solid #ffffff;
    outline-offset: -10px;
    border-radius: 2px; }

  &__button {
    text-align: center; } }

.sale {
  padding: 40px 20px 45px;
  margin-bottom: 56px;
  @include latob;
  line-height: 1;
  text-align: center;
  text-transform: uppercase;

  &__season {
    margin: 0;
    margin-bottom: 40px;
    font-size: 30px;
    color: #fff; }

  &__title {
    margin: 0;
    margin-bottom: 35px;
    font-size: 59px;
    color: #000; }

  &__upto {
    position: relative;

    margin: 0;
    margin-bottom: 25px;
    font-size: 20px;
    color: #fff;

    > span {
      position: relative; }

    &:before {
      content: '';
      display: block;
      width: 170px;
      height: 1px;
      position: absolute;
      top: 8px;
      left: 50%;
      transform: translateX(-50%);
      background: linear-gradient(90deg, #fff 40px, transparent 0, transparent 130px, #fff 0); }

    &:after {
      content: '';
      display: block;
      width: 210px;
      height: 1px;
      position: absolute;
      top: auto;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      background: linear-gradient(90deg, #fff 60px, transparent 0, transparent 150px, #fff 0); } }

  &__size {
    margin: 0;
    color: #000;
    font-size: 31px;

    > span {
      position: relative;
      left: -5px;
      font-size: 24px; } } }


.button-shop {
  height: 53px;
  border: 0;
  padding-right: 25px;
  padding-left: 25px;

  @include latob;
  font-size: 24px;

  text-transform: uppercase;
  color: #fff;
  background-color: #f09d66; }
