$fields: 10px;
$max_width: 940px;
$offset: 20px;
$columns: 12;
$offset_one_side: ($offset / 2);


@mixin wrapper-full() {
  padding-left: $fields;
  padding-right: $fields; }

@mixin container() {
  max-width: $max_width;
  margin: 0 auto;
  @include wrapper-full(); }

@mixin row-offsets() {
  margin-left: ($offset_one_side * -1);
  margin-right: ($offset_one_side * -1); }

@mixin row-flex() {
  @include row-offsets(); }

@mixin row-float() {
  @include row-offsets();
  @include clearfix(); }

@mixin col-offsets($type) {
  #{$type}-left: $offset_one_side;
  #{$type}-right: $offset_one_side; }

@mixin col() {
  box-sizing: border-box;
  word-wrap: break-word;
  @include col-offsets(margin); }

@mixin col-float() {
  float: left; }

@mixin col-padding() {
  word-wrap: break-word;
  @include col-offsets(padding); }

@mixin row-ib() {
  @include row-offsets(); }

@mixin col-ib() {
  display: inline-block;
  vertical-align: top; }

@mixin sz($n) {
  $val: 100% / $columns * $n;
  width: #{calc(#{$val} - #{$offset})}; }

@mixin shift($n) {
  $val: 100% / $columns * $n;
  margin-left: #{calc(#{$val} + #{$offset_one_side})};
  margin-right: #{calc(#{$val} + #{$offset_one_side})}; }

@mixin shift-left($n) {
  $val: 100% / $columns * $n;
  margin-left: #{calc(#{$val} + #{$offset_one_side})}; }

@mixin shift-right($n) {
  $val: 100% / $columns * $n;
  margin-right: #{calc(#{$val} + #{$offset_one_side})}; }

@mixin shift-padding($n) {
  $val: 100% / $columns * $n;
  padding-left: #{calc(#{$val} + #{$offset_one_side})};
  padding-right: #{calc(#{$val} + #{$offset_one_side})}; }

@mixin shift-padding-left($n) {
  $val: 100% / $columns * $n;
  padding-left: #{calc(#{$val} + #{$offset_one_side})}; }

@mixin shift-padding-right($n) {
  $val: 100% / $columns * $n;
  padding-right: #{calc(#{$val} + #{$offset_one_side})}; }

@mixin debug($background, $outline) {
  height: 100vh;
  position: fixed;
  width: 100%;

  > div {
    height: inherit;
    @include wrapper();

    > div {
      height: inherit;
      @include row-flex();

      > div {
        @include col();
        @include size(1);
        height: inherit;
        outline: $outline;
        background: $background; } } } }

@mixin clearfix() {
  &::after {
    display: block;
    content: "";
    clear: both; } }

