.header {

  &__wrap {
    position: relative;
    display: flex;
    height: 127px;
    align-items: center; }

  &__menu {
    margin-right: auto; }

  &__logo {
    position: absolute;
    top: 55px;
    left: 50%;
    transform: translateX(-50%); }

  &__sociate {
    margin-right: 50px; } }


.main-nav {

  &__list {
    display: flex;
    list-style: none; }

  &__item {
    @include playi;
    font-size: 18px;
    line-height: 1;
    color: #000;

    &:not(:last-child) {
      margin-right: 20px; } }

  a {
    text-decoration: none;
    color: inherit; } }

.logo {
  display: block;
  padding: 0;

  @include krink;
  font-size: 72px;
  line-height: 1;

  text-decoration: none;
  color: #000; }

.socicons {
  display: flex;

  &__item {
    position: relative;
    width: 36px;
    height: 36px;

    border: 2px solid #d9bf8f;
    border-radius: 50%;

    &:not(:last-child) {
      margin-right: 10px; } }

  .icon {
    @include vcenter;
    width: 17px;
    height: 17px;
    fill: #d9bf8f; } }


.current-button {
  width: 92px;
  height: 37px;

  @include latob;
  font-size: 12px;

  text-transform: uppercase;
  background-color: black;
  border: 1px solid #fffcfc;
  color: #ffffff;

  span {
    position: relative;
    left: -9px;

    &:before {
      content: '';
      position: absolute;
      top: 2px;
      right: -17px;

      border: solid #ffffff;
      border-width: 0 1px 1px 0;
      display: inline-block;
      padding: 3px;
      transform: rotate(45deg); } } }
