.top-bar {
  padding: 14px 0;
  line-height: 1;
  background-color: #f2f2f2;

  &__wrap {
    display: flex;
    align-items: center; }

  &__menu {
    margin-left: auto; } }

.top-menu {
  &__list {
    display: flex;
    margin: 0;
    padding: 0;
    list-style: none; }

  &__item {
    text-transform: uppercase;

    &:not(:last-child) {
      padding-right: 20px;
      margin-right: 20px;
      border-right: 1px solid $main; } }

  a {
    text-decoration: none;
    color: inherit; } }

.phone {
  position: relative;
  padding-left: 25px;

  &:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: rotate3d(0, 1, 0, 180deg) translateY(-50%);

    display: block;
    width: 12px;
    height: 12px;

    background: url(../img/phone.svg) no-repeat center;
    background-size: contain; }

  a {
    color: inherit;
    text-decoration: none; } }
