.features {
  display: flex;
  margin-bottom: 80px;
  justify-content: space-between; }

.feature {
  position: relative;

  display: flex;
  align-items: center;
  width: 30%;
  height: 100px;
  color: #fff;
  background-color: #d9bf8f;

  $triangleHeight: 45px;

  &--left {
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 100%;
      z-index: 10;

      display: block;
      width: 30px;
      height: 30px;
      border: 50px solid transparent;
      border-left: $triangleHeight solid #d9bf8f;
      border-right: transparent; } }

  &--right {
    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: 100%;
      z-index: 10;

      display: block;
      width: 30px;
      height: 30px;
      border: 50px solid transparent;
      border-right: $triangleHeight solid #d9bf8f;
      border-left: transparent; } }

  &--middle {
    min-width: #{ calc(390px + 2*40px) };
    padding: 0 30px;
    background-color: #000;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;

      display: block;
      width: 30px;
      height: 30px;
      border: 50px solid transparent;
      border-right: $triangleHeight solid #fff;
      border-left: transparent; }

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;

      display: block;
      width: 30px;
      height: 30px;
      border: 50px solid transparent;
      border-left: $triangleHeight solid #fff;
      border-right: transparent; } }

  &__icon {
    width: 100px;

    .icon {
      position: relative;
      left: 30px;
      width: 40px;
      height: 40px;
      fill: #fff; } }

  &__icon--plane {
    top: -12px;
    transform: rotate3d(0, 0, 1, -45deg); }

  &__title {
    margin: 0;
    font-size: 30px;
    font-weight: 700; }

  &__descr {
    margin: 0;
    font-size: 14px; } }




