
// Mediaquery
@mixin from($min_width) {
  @media screen and (min-width: $min_width + 'px') {
    @content; } }

@mixin to($max_width) {
  @media screen and (max-width: $max_width + 'px') {
    @content; } }

@mixin from-to($min_width, $max_width) {
  @media screen and (min-width: $min_width) and (max-width: $max_width) {
    @content; } }

@mixin retina {
  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    @content; } }




// font-family
@mixin font($alias, $name, $weigth: normal, $style: normal) {
  @font-face {
    font-family: $alias;
    src: url("../fonts/" + $name + ".woff2") format("woff2"), url("../fonts/" + $name + ".woff") format("woff");
    font-weight: $weigth;
    font-style: $style; } }
// to use:
//+font(black, cytiapro-black-webfont)

@mixin w($col, $container) {
  width: round($col / $container * 100%); }

@mixin ie {
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    @content; } }


// Display flex wrap
@mixin df {
  display: flex;
  flex-wrap: wrap; }

// .visually-hidden
@mixin vh() {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

@mixin revh() {
  position: statick;
  clip: auto;
  width: auto;
  height: auto;
  margin: 0; }

// Center container
@mixin vcenter() {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%); }

@mixin bg {
  background: rgba(pink, .4); }

//flip-horizontal
@mixin fg {
  transform: scale(-1, 1); }

//flip-vertical
@mixin fg {
  transform: scale(1, -1); }

//placeholder
@mixin placeholder {
  &::placeholder {
    @content; } }

@mixin placeholder-color($color) {
  &::placeholder {
    color: $color; } }

// ROTATE
@mixin rotate($deg: 0, $m11: 0, $m12: 0, $m21: 0, $m22: 0) {
  transform: rotate($deg + deg);
  filter: progid:DXImageTransform.Microsoft.Matrix(M11=$m11, M12=$m12,M21=$m21, M22=$m22, sizingMethod='auto expand');
  zoom: 1; }

// TRANSITION

@mixin tr($what, $time: .3s, $how: ease) {
  transition: $what $time $how; }
