.reviews {
  position: relative;
  background-color: #d9bf8f;
  padding: 40px 0;

  &__wrap {
    display: flex;
    justify-content: space-between; }

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 10;

    display: block;
    border: 200px solid transparent;
    border-top: 40px solid #fff;
    border-bottom: transparent; } }


.review {
  display: flex;
  align-items: flex-end;
  width: 45%;

  &__img {
    min-width: 254px; }

  &__says {
    margin-bottom: 40px;
    @include playi;
    font-size: 18px;
    color: #fff; }

  &__name {
    margin-bottom: 0;
    @include playbi;
    font-size: 18px;
    line-height: 1.2; }

  &__status {
    font-size: 14px;
    font-weight: 700;

    text-transform: uppercase;
    color: #7f7e7e; } }




.review--right {
  text-align: right;

  .review__img {
    order: 2; } }
