.section {
  margin-bottom: 100px;

  &--reviews {
    margin-bottom: 0; }

  &__head {
    position: relative;

    margin-bottom: 60px;
    text-align: center;

    &:before {
      content: '';
      @include vcenter;
      display: block;
      width: 100%;
      height: 1px;
      background-color: #eaeaea; } }

  &__title {
    position: relative;
    z-index: 1;

    display: inline-block;
    padding: 0 150px;
    margin-top: 0;
    margin-bottom: 0;

    @include playbi;
    font-size: 30px;
    line-height: 18px;
    text-align: center;

    background-color: #fff;

    &:before,
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 50px;
      display: block;
      width: 20px;
      height: 20px;
      background: #d9bf8f;

      transform: rotate3d(0, 0, 1, 45deg); }

    &:after {
      left: auto;
      right: 50px; } } }


.brands {
  display: flex;
  justify-content: space-between; }


.footer {
  padding-top: 50px;
  padding-bottom: 80px;
  border-bottom: 40px solid #000;
  background-color: #141414;
  color: #737373;

  a {
    text-decoration: none;
    color: inherit;

    &:hover {
      text-decoration: underline; } }

  &__top {
    @include df;
    @include row-flex;
    padding-bottom: 70px;
    border-bottom: 1px solid #737373;

    position: relative;

    &:after {
      content: '';
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translateX(-50%);
      display: block;
      width: 30px;
      height: 30px;
      border: 20px solid transparent;
      border-top: 10px solid #737373;
      border-bottom: transparent; } }

  &__bottom {
    padding: 30px 0; }

  &__menu {
    @include col;
    @include sz(2); }

  &__social {
    @include col;
    @include sz(6);
    padding-left: 40px; }

  &__icons {
    margin-bottom: 30px; }

  &__title {
    margin-top: 0;
    margin-bottom: 30px;
    font-size: 16px;
    font-weight: 700;

    text-transform: uppercase;
    color: #fff; }

  &__pay {
    margin-top: 30px; } }

.menu {

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;

    line-height: 30px; } }


.soc-icons {
  display: flex; }

.soc-icon {
  margin-right: 15px; }


.search {
  width: 365px;
  height: 45px;
  padding: 2px;
  border: 1px solid #737373;

  display: flex;

  &__input {
    flex-grow: 1;
    border: 0;
    background-color: transparent;

    &::placeholder {
      padding: 0 20px;
      font-size: 12px;
      color: #6a6a6a; }

    &:focus {
      outline: 0; } }

  &__button {
    width: 104px;
    border: 0;

    color: #fff;
    background-color: #323030;

    cursor: pointer;

    &:focus {
      outline: 0; } } }


.pay {
  display: flex;
  align-items: center;

  &__icon {
    margin-right: 20px; } }

