@include font(LatoRegular, LatoRegular);
@include font(LatoRegular, LatoBold, 700);
@include font(krinkesdecorpersonal, krinkesdecorpersonal);
@include font(playfairdisplay, playfairdisplay);
@include font(playfairdisplay, playfairdisplaybold, 700);
@include font(playfairdisplay, playfairdisplayitalic, normal, italic);
@include font(playfairdisplay, playfairdisplaybolditalic, 700, italic);

@mixin lato {
  font-family: LatoRegular, sans-serif;
  font-weight: normal; }

@mixin krink {
  font-family: krinkesdecorpersonal;
  font-weight: normal; }

@mixin latob {
  font-family: LatoRegular, sans-serif;
  font-weight: 700; }

@mixin play {
  font-family: playfairdisplay, sans-serif;
  font-weight: normal; }

@mixin playb {
  font-family: playfairdisplay, sans-serif;
  font-weight: 700; }

@mixin playi {
  font-family: playfairdisplay, sans-serif;
  font-weight: normal;
  font-style: italic; }

@mixin playbi {
  font-family: playfairdisplay, sans-serif;
  font-weight: 700;
  font-style: italic; }


