.products {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.product {
  position: relative;
  overflow: hidden;
  width: 310px;
  margin-bottom: 20px;
  background-size: cover;

  &__wrap {
    padding-bottom: 115%; }

  &__sale {
    $rad: 60px;
    position: absolute;
    top: 20px;
    left: 20px;

    display: block;
    width: $rad;
    height: $rad;

    font-size: 21px;
    font-weight: 700;
    line-height: $rad;
    text-align: center;

    border-radius: 50%;
    color: #fff;
    background: #feab12; }

  &__button {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 0; } }

.add-cart {
  width: 198px;
  height: 44px;
  border: 0;
  outline: 0;

  text-transform: uppercase;

  color: #fff;
  background: transparent;

  cursor: pointer;

  position: relative;

  &:before {
    content: '';
    position: absolute;
    bottom: 2px;
    left: 0;
    z-index: -1;
    display: block;
    width: 100%;
    height: 100%;

    background-color: #000;

    transform: perspective(5px) rotateX(3deg);

    @include tr(background-color, .3s); }

  &--active,
  &:hover {
    &:before {
      background-color: #f0975c; } }

  &:active {
    &:before {
      background-color: lighten(#f0975c, 10%); } } }
